<template>
    <nx-container class="page">
        <template slot="header">数字动画组件</template>
        <el-row :gutter="20">
            <el-col :span="6">
                <el-card shadow="never">
                    <p slot="title">只设置目标数字</p>
                    <div class="group">
                        <nx-count-up :end="100"></nx-count-up>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card shadow="never">
                    <p slot="title">设置起止数值</p>
                    <div class="group">
                        <nx-count-up :start="14" :end="100"></nx-count-up>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card shadow="never">
                    <p slot="title">小数位数</p>
                    <div class="group">
                        <nx-count-up :end="100" :decimals="2"></nx-count-up>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card shadow="never">
                    <p slot="title">动画时长</p>
                    <div class="group">
                        <nx-count-up :end="100" :duration="6"></nx-count-up>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card shadow="never">
                    <p slot="title">动画时长</p>
                    <div class="group">
                        <nx-count-up :end="100" :duration="6"></nx-count-up>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card shadow="never">
                    <p slot="title">回调函数</p>
                    <div class="group">
                        <nx-count-up :end="100" :callback="() => {className='end'}" :class="className"></nx-count-up>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card shadow="never" class="d2-mb-0">
                    <p slot="title">结束一秒后更新数值</p>
                    <div class="group">
                        <nx-count-up :end="end" :callback="update"></nx-count-up>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </nx-container>
</template>

<script>
import nxContainer from '@/components/nx-container'
import nxCountUp from '@/components/nx-count-up'
export default {
    components: {
        nxCountUp,
        nxContainer
    },
    data() {
        return {
            // 回调函数使用
            className: '',
            // 更新数值用
            end: 50
        }
    },
    methods: {
        update() {
            setTimeout(() => {
                this.end = 100
            }, 1000)
        }
    }
}
</script>
<style lang="scss" scoped>
@import '~@/assets/style/public.scss';
.page {
  .el-card {
    @extend %unable-select;
    .group {
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        font-size: 60px;
        &.end {
          padding: 0px 20px;
          border-radius: 4px;
          background-color: $color-success;
          color: #FFF;
        }
      }
    }
  }
}
</style>






